body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #323232; }

::-moz-selection {
  background: rgba(15, 201, 108, 0.5); }

::selection {
  background: rgba(15, 201, 108, 0.5); }

.vAlign {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.home {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/grip-wildtiger-client/static/media/home-bg.65249cef.png);
  background-size: cover;
  background-position: center;
  background-attachment: fixed; }

.menu {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/grip-wildtiger-client/static/media/menu-bg.d7029b02.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed; }

.reservation {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/grip-wildtiger-client/static/media/reservation-bg.820082a0.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed; }

.outside {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/grip-wildtiger-client/static/media/outside-edit.83e7f258.png);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-attachment: inherit; }

header {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  position: relative; }

header::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 8%;
  background-image: url(/grip-wildtiger-client/static/media/brush-stroke.75c039ee.svg);
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 100%;
  left: 0;
  -webkit-transform: translateY(-40%);
          transform: translateY(-40%);
  z-index: 5; }

section::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 8%;
  background-image: url(/grip-wildtiger-client/static/media/brush-stroke.75c039ee.svg);
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 100%;
  left: 0;
  -webkit-transform: translateY(-40%);
          transform: translateY(-40%);
  z-index: 5; }

.hero {
  text-align: center; }

.shadow {
  text-shadow: 0px 3px rgba(0, 0, 0, 0.5); }

.hero__sub {
  margin: 0;
  font-weight: 500;
  font-size: 1.7rem;
  color: white; }

.hero__main {
  margin: 0;
  font-weight: 100;
  font-size: 4.4rem;
  font-family: "Kaushan Script", cursive;
  color: white; }

.cta {
  background: #0fc96c;
  color: white;
  display: inline-block;
  border: none;
  font-size: 1.4rem;
  font-weight: 500;
  padding: 0.7em 1em;
  border-radius: 10em;
  margin-top: 1.2em;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.6);
  cursor: pointer;
  transition: all 0.2s ease-in; }

.cta:hover,
.cta:focus {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.8); }

section {
  padding: 5rem 0;
  background: white;
  position: relative; }

nav {
  color: white;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  z-index: 100;
  padding: 1.5em 0px; }

.nav-logo {
  font-weight: 800;
  text-decoration: none;
  color: white;
  font-size: 1.6rem; }

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.main-nav {
  margin: 0;
  padding: 0; }

.main-nav li {
  display: inline-block;
  margin-left: 40px; }

.main-nav li:first-child {
  margin: 0; }

.main-nav li a {
  color: #e6e6e6;
  font-weight: 500;
  font-size: 1.1rem;
  text-decoration: none;
  position: relative; }

.main-nav li a.active {
  color: white; }

.main-nav li a:hover {
  color: white; }

.main-nav li a.active::after {
  content: "";
  height: 0.15rem;
  width: 100%;
  background: white;
  display: block;
  position: absolute;
  top: 100%; }

.block-head {
  text-align: center;
  background: url(/grip-wildtiger-client/static/media/paw.661c0fcd.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  padding: 1rem; }

.block__heading {
  font-family: "Kaushan Script", cursive;
  font-size: 2.5rem;
  color: #0fc96c;
  margin: 0 0 -0.5rem 0; }

.block__subheading {
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 800;
  color: #282828;
  margin: 0; }

.block-body-columns p {
  text-align: justify;
  -webkit-column-count: 2;
          column-count: 2;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem; }

.block-body p {
  text-align: center; }

.block-link {
  font-weight: 500;
  display: inline-block;
  text-transform: uppercase;
  color: #282828;
  text-decoration: none;
  position: relative;
  margin-top: 1em; }

.block-link::after {
  content: "";
  background: #282828;
  display: block;
  width: 100%;
  height: 0.1rem;
  position: absolute;
  top: 100%; }

.block-link:hover {
  color: #0fc96c; }

.block-link:hover::after {
  background: #0fc96c; }

.widescreen-ratio {
  background-color: #a0a0a0;
  position: relative;
  width: 100%;
  padding-top: 75%;
  /* 4:3 Aspect Ratio */ }

.box {
  height: 400px; }

.dark-section {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/grip-wildtiger-client/static/media/wood-bg-edit.ca9536f5.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed; }

.map-section {
  background-image: url(/grip-wildtiger-client/static/media/map.c417c591.png);
  background-size: cover;
  background-position: center;
  background-attachment: fixed; }

.schedule {
  background: #0fc96c;
  padding: 1.5rem;
  display: inline-block;
  position: relative; }
  .schedule .schedule__heading {
    margin: 0px;
    color: white;
    font-family: "Kaushan Script", cursive;
    text-transform: capitalize;
    font-size: 1.5rem;
    text-align: center; }
  .schedule .schedule__subheading {
    margin: 0px;
    color: white;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 1.5rem;
    text-align: center;
    padding-bottom: 1.4rem; }
  .schedule .schedule__timings-row {
    display: flex;
    justify-content: space-between; }
    .schedule .schedule__timings-row span {
      color: white;
      font-size: 1.2rem; }
    .schedule .schedule__timings-row span.day {
      padding-right: 1.5rem; }

.schedule::after {
  content: '';
  display: block;
  position: absolute;
  left: 100%;
  top: 50%;
  border-left: 1rem solid #0fc96c;
  border-right: 1rem solid transparent;
  border-top: 1rem solid transparent;
  border-bottom: 1rem solid transparent;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

